import androidx.compose.runtime.CompositionLocalProvider
import com.varabyte.kobweb.core.AppGlobalsLocal
import com.varabyte.kobweb.navigation.RoutePrefix
import com.varabyte.kobweb.navigation.Router
import com.varabyte.kobweb.navigation.UpdateHistoryMode
import com.varabyte.kobweb.silk.defer.renderWithDeferred
import kotlin.Unit
import kotlinx.browser.document
import kotlinx.browser.window
import org.jetbrains.compose.web.renderComposable

public fun main(): Unit {
    RoutePrefix.set("")
    val router = Router()
    com.varabyte.kobweb.core.init.initKobweb(router) { ctx ->
        ctx.router.register("/") { com.ahmedhnewa.alrayada_landing_page.pages.HomePage() }
        ctx.router.register("/download-mobile-app") {
                com.ahmedhnewa.alrayada_landing_page.pages.DownloadMobileAppPage() }
        ctx.router.register("/privacy-policy") {
                com.ahmedhnewa.alrayada_landing_page.pages.PrivacyPolicyPage() }

    }
    router.addRouteInterceptor {
        path = path.removeSuffix(".html").removeSuffix(".htm")
    }

    com.varabyte.kobweb.silk.init.additionalSilkInitialization = { ctx ->
        com.varabyte.kobweb.silk.init.initSilkWidgets(ctx)
        com.varabyte.kobweb.silk.init.initSilkWidgetsKobweb(ctx)
        ctx.theme.registerComponentStyle(com.ahmedhnewa.alrayada_landing_page.components.BackToTopButtonStyle)
        ctx.theme.registerComponentStyle(com.ahmedhnewa.alrayada_landing_page.components.CheckIndicatorStyle)
        ctx.theme.registerComponentStyle(com.ahmedhnewa.alrayada_landing_page.components.NavigationIconStyle)
        ctx.theme.registerComponentStyle(com.ahmedhnewa.alrayada_landing_page.sections.about.AboutImageStyle)
        ctx.theme.registerComponentStyle(com.ahmedhnewa.alrayada_landing_page.sections.contact.components.MyTextInputStyle)
        ctx.theme.registerComponentStyle(com.ahmedhnewa.alrayada_landing_page.sections.main.helloTextStyle)
        ctx.theme.registerComponentStyle(com.ahmedhnewa.alrayada_landing_page.sections.main.MainImageStyle)
        ctx.theme.registerComponentStyle(com.ahmedhnewa.alrayada_landing_page.sections.main.compoments.NavigationItemStyle)
        ctx.theme.registerComponentStyle(com.ahmedhnewa.alrayada_landing_page.sections.main.compoments.BrandingLogoStyle)
        ctx.theme.registerComponentStyle(com.ahmedhnewa.alrayada_landing_page.sections.main.compoments.SocialLinkItemStyle)
        ctx.theme.registerComponentStyle(com.ahmedhnewa.alrayada_landing_page.sections.mobile_app.componenets.DownloadMobileAppButtonStyle)
        ctx.theme.registerComponentStyle(com.ahmedhnewa.alrayada_landing_page.sections.objective.components.ObjectiveCardStyle)
        ctx.theme.registerComponentStyle(com.ahmedhnewa.alrayada_landing_page.sections.portfolio.PortfolioSectionStyle)
        ctx.theme.registerComponentStyle(com.ahmedhnewa.alrayada_landing_page.sections.portfolio.PortfolioArrowIconStyle)
        ctx.theme.registerComponentStyle(com.ahmedhnewa.alrayada_landing_page.sections.products.components.ProductImageStyle)
        ctx.theme.registerComponentStyle(com.ahmedhnewa.alrayada_landing_page.sections.service.ServiceCardStyle)
        ctx.theme.registerComponentStyle(com.ahmedhnewa.alrayada_landing_page.styles.MainButtonStyle)
        ctx.theme.registerComponentStyle(com.ahmedhnewa.alrayada_landing_page.styles.MySelectStyle)
        ctx.theme.registerComponentStyle(com.ahmedhnewa.alrayada_landing_page.styles.DescTextStyle1)
        ctx.theme.registerComponentStyle(com.ahmedhnewa.alrayada_landing_page.styles.ReadableTextStyleOnWhiteBackground)
        com.ahmedhnewa.alrayada_landing_page.initSilk(ctx)
    }

    // For SEO, we may bake the contents of a page in at build time. However, we will
    // overwrite them the first time we render this page with their composable, dynamic
    // versions. Think of this as poor man's hydration :)
    // See also: https://en.wikipedia.org/wiki/Hydration_(web_development)
    router.navigateTo(window.location.href.removePrefix(window.location.origin),
            UpdateHistoryMode.REPLACE)

    val root = document.getElementById("root")!!
    while (root.firstChild != null) { root.removeChild(root.firstChild!!) }

    renderComposable(rootElementId = "root") {
        CompositionLocalProvider(AppGlobalsLocal provides mapOf("title" to "Alrayada")) {
            com.ahmedhnewa.alrayada_landing_page.MyApp {
                router.renderActivePage { renderWithDeferred { it() } }
            }
        }
    }
}
